import { useState, useEffect, useContext, useRef } from "react";
import { AuthContext } from "../context/AuthContext";
import axios from "axios";
import { io } from "socket.io-client";

const socket = io("http://localhost:5000");

export default function AdminPrivateChat() {
  const { user } = useContext(AuthContext);
  const [users, setUsers] = useState([]);
  const [selectedUser, setSelectedUser] = useState(null);
  const [messages, setMessages] = useState([]);
  const [reply, setReply] = useState("");
  const [typingUser, setTypingUser] = useState(null);
  const [onlineUsers, setOnlineUsers] = useState([]);
  const messagesEndRef = useRef(null);
  let typingTimeout = null;

  useEffect(() => {
    axios
      .get("http://localhost:5000/api/private/users", {
        headers: { Authorization: `Bearer ${user.token}` },
      })
      .then((res) => setUsers(res.data));

    socket.on("onlineUsers", (users) => setOnlineUsers(users));
    return () => socket.off("onlineUsers");
  }, [user]);

  const loadMessages = (userId) => {
    setSelectedUser(userId);
    socket.emit("joinRoom", userId);
    axios
      .get(`http://localhost:5000/api/private/${userId}`, {
        headers: { Authorization: `Bearer ${user.token}` },
      })
      .then((res) => setMessages(res.data));
  };

  useEffect(() => {
    socket.on("privateMessage", (msg) => {
      if (msg.from.id === selectedUser || msg.to.id === selectedUser) {
        setMessages((prev) => [...prev, msg]);
      }
    });

    socket.on("typing", (alias) => setTypingUser(alias));
    socket.on("stopTyping", () => setTypingUser(null));

    socket.on("messageRead", (msgId) =>
      setMessages((prev) =>
        prev.map((m) => (m._id === msgId ? { ...m, status: "read" } : m))
      )
    );

    return () => socket.removeAllListeners();
  }, [selectedUser]);

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  const sendReply = async () => {
    if (!reply.trim()) return;
    await axios.post(
      "http://localhost:5000/api/private/reply",
      { userId: selectedUser, message: reply },
      { headers: { Authorization: `Bearer ${user.token}` } }
    );
    setReply("");
  };

  const handleTyping = (e) => {
    setReply(e.target.value);
    socket.emit("typing", selectedUser, user.alias);
    clearTimeout(typingTimeout);
    typingTimeout = setTimeout(
      () => socket.emit("stopTyping", selectedUser),
      2000
    );
  };

  const handleKeyPress = (e) => {
    if (e.key === "Enter" && !e.shiftKey) {
      e.preventDefault();
      sendReply();
    }
  };

  const selectedUserData = users.find((u) => u._id === selectedUser);
  const isUserOnline =
    selectedUserData && onlineUsers.includes(selectedUserData.alias);

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <div className="bg-white shadow-sm border-b border-gray-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
          <h1 className="text-3xl font-bold text-gray-900">
            Private Chat Admin
          </h1>
          <p className="mt-1 text-sm text-gray-600">
            Chat pribadi dengan users untuk support dan moderasi
          </p>
        </div>
      </div>

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
        <div className="flex gap-6 h-screen max-h-[calc(100vh-200px)]">
          {/* Users Sidebar */}
          <div className="w-80 bg-white rounded-lg shadow-sm border border-gray-200 overflow-hidden">
            <div className="p-4 border-b border-gray-200 bg-gray-50">
              <h3 className="text-lg font-medium text-gray-900">Users</h3>
              <p className="text-sm text-gray-600">
                {users.length} total users
              </p>
            </div>

            <div className="overflow-y-auto h-full">
              {users.length === 0 ? (
                <div className="p-8 text-center">
                  <div className="text-4xl mb-4"></div>
                  <p className="text-gray-500">Tidak ada users</p>
                </div>
              ) : (
                <div className="p-2 space-y-1">
                  {users.map((u) => {
                    const isOnline = onlineUsers.includes(u.alias);
                    const isSelected = selectedUser === u._id;

                    return (
                      <div
                        key={u._id}
                        onClick={() => loadMessages(u._id)}
                        className={`p-3 rounded-lg cursor-pointer transition-colors ${
                          isSelected
                            ? "bg-blue-100 border-2 border-blue-300"
                            : "hover:bg-gray-50 border-2 border-transparent"
                        }`}
                      >
                        <div className="flex items-center space-x-3">
                          <div className="relative">
                            <div className="w-10 h-10 bg-gray-300 rounded-full flex items-center justify-center">
                              <span className="text-sm font-medium text-gray-700">
                                {u.alias?.charAt(0)?.toUpperCase()}
                              </span>
                            </div>
                            {isOnline && (
                              <div className="absolute -bottom-1 -right-1 w-4 h-4 bg-green-500 rounded-full border-2 border-white"></div>
                            )}
                          </div>
                          <div className="flex-1 min-w-0">
                            <p
                              className={`text-sm font-medium truncate ${
                                isSelected ? "text-blue-900" : "text-gray-900"
                              }`}
                            >
                              {u.alias}
                            </p>
                            <div className="flex items-center space-x-2">
                              <span
                                className={`inline-flex px-2 py-0.5 text-xs font-semibold rounded-full ${
                                  u.role === "admin"
                                    ? "bg-purple-100 text-purple-700"
                                    : "bg-blue-100 text-blue-700"
                                }`}
                              >
                                {u.role}
                              </span>
                              {isOnline && (
                                <span className="text-xs text-green-600 font-medium">
                                  Online
                                </span>
                              )}
                            </div>
                          </div>
                        </div>
                      </div>
                    );
                  })}
                </div>
              )}
            </div>
          </div>

          {/* Chat Area */}
          <div className="flex-1 bg-white rounded-lg shadow-sm border border-gray-200 overflow-hidden flex flex-col">
            {selectedUser ? (
              <>
                {/* Chat Header */}
                <div className="p-4 border-b border-gray-200 bg-gray-50">
                  <div className="flex items-center space-x-3">
                    <div className="relative">
                      <div className="w-10 h-10 bg-gray-300 rounded-full flex items-center justify-center">
                        <span className="text-sm font-medium text-gray-700">
                          {selectedUserData?.alias?.charAt(0)?.toUpperCase()}
                        </span>
                      </div>
                      {isUserOnline && (
                        <div className="absolute -bottom-1 -right-1 w-4 h-4 bg-green-500 rounded-full border-2 border-white"></div>
                      )}
                    </div>
                    <div>
                      <h3 className="text-lg font-medium text-gray-900">
                        {selectedUserData?.alias}
                      </h3>
                      <p className="text-sm text-gray-600">
                        {isUserOnline ? " Online" : " Offline"} {" "}
                        {selectedUserData?.role}
                      </p>
                    </div>
                  </div>
                </div>

                {/* Messages */}
                <div className="flex-1 overflow-y-auto p-4 space-y-4 bg-gray-50">
                  {messages.length === 0 ? (
                    <div className="text-center text-gray-500 mt-20">
                      <div className="text-4xl mb-4"></div>
                      <h3 className="text-lg font-medium mb-2">
                        Belum ada percakapan
                      </h3>
                      <p className="text-sm">
                        Mulai chat dengan {selectedUserData?.alias}
                      </p>
                    </div>
                  ) : (
                    messages.map((m) => {
                      const isMe = m.from.id === user.id;
                      const time = new Date(m.createdAt).toLocaleTimeString(
                        [],
                        {
                          hour: "2-digit",
                          minute: "2-digit",
                        }
                      );

                      return (
                        <div
                          key={m._id}
                          className={`flex ${
                            isMe ? "justify-end" : "justify-start"
                          }`}
                        >
                          <div
                            className={`max-w-xs lg:max-w-md ${
                              isMe ? "order-1" : ""
                            }`}
                          >
                            {/* Avatar & Name */}
                            <div
                              className={`flex items-center mb-1 ${
                                isMe ? "justify-end" : ""
                              }`}
                            >
                              <div
                                className={`w-6 h-6 rounded-full flex items-center justify-center text-xs font-medium text-white ${
                                  isMe
                                    ? "bg-green-500 ml-2"
                                    : "bg-blue-500 mr-2"
                                }`}
                              >
                                {isMe ? "" : ""}
                              </div>
                              <span className="text-xs text-gray-600 font-medium">
                                {m.from.alias}
                              </span>
                              {isMe && (
                                <span className="ml-2 px-2 py-0.5 bg-green-100 text-green-700 text-xs rounded-full">
                                  Admin
                                </span>
                              )}
                            </div>

                            {/* Message bubble */}
                            <div
                              className={`rounded-lg px-4 py-3 ${
                                isMe
                                  ? "bg-green-600 text-white"
                                  : "bg-white text-gray-900 border border-gray-200"
                              }`}
                            >
                              <p className="text-sm leading-relaxed">
                                {m.message}
                              </p>

                              {/* Time & Status */}
                              <div
                                className={`flex items-center justify-between mt-2 ${
                                  isMe ? "text-green-100" : "text-gray-500"
                                }`}
                              >
                                <span className="text-xs">{time}</span>
                                <span className="text-xs">
                                  {m.status === "read" ? "" : ""}
                                </span>
                              </div>
                            </div>
                          </div>
                        </div>
                      );
                    })
                  )}
                  <div ref={messagesEndRef} />
                </div>

                {/* Typing Indicator */}
                {typingUser && (
                  <div className="px-4 py-2 bg-white border-t border-gray-100">
                    <div className="flex items-center text-sm text-gray-500">
                      <div className="flex space-x-1 mr-2">
                        <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce"></div>
                        <div
                          className="w-2 h-2 bg-gray-400 rounded-full animate-bounce"
                          style={{ animationDelay: "0.1s" }}
                        ></div>
                        <div
                          className="w-2 h-2 bg-gray-400 rounded-full animate-bounce"
                          style={{ animationDelay: "0.2s" }}
                        ></div>
                      </div>
                      <span className="italic">
                        {typingUser} sedang mengetik...
                      </span>
                    </div>
                  </div>
                )}

                {/* Input Area */}
                <div className="border-t border-gray-200 p-4 bg-white">
                  <div className="flex space-x-3">
                    <div className="flex-1">
                      <input
                        type="text"
                        value={reply}
                        onChange={handleTyping}
                        onKeyPress={handleKeyPress}
                        placeholder="Balas pesan..."
                        className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent outline-none transition-all"
                      />
                    </div>
                    <button
                      onClick={sendReply}
                      disabled={!reply.trim()}
                      className={`px-6 py-2 rounded-lg font-medium transition-all ${
                        !reply.trim()
                          ? "bg-gray-300 text-gray-500 cursor-not-allowed"
                          : "bg-green-600 hover:bg-green-700 text-white shadow-sm hover:shadow-md"
                      }`}
                    >
                      <svg
                        className="w-4 h-4"
                        fill="none"
                        stroke="currentColor"
                        viewBox="0 0 24 24"
                      >
                        <path
                          strokeLinecap="round"
                          strokeLinejoin="round"
                          strokeWidth={2}
                          d="M12 19l9 2-9-18-9 18 9-2zm0 0v-8"
                        />
                      </svg>
                    </button>
                  </div>
                </div>
              </>
            ) : (
              /* No User Selected */
              <div className="flex-1 flex items-center justify-center">
                <div className="text-center text-gray-500">
                  <div className="text-6xl mb-4"></div>
                  <h3 className="text-xl font-medium mb-2">
                    Pilih User untuk Chat
                  </h3>
                  <p className="text-sm">
                    Klik nama user di sidebar untuk mulai percakapan
                  </p>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}
