import { useState, useEffect, useContext, useCallback } from "react";
import {
  getUsers,
  approveUser,
  deleteUser,
  getChats,
  deleteChat,
} from "../services/adminService";
import { AuthContext } from "../context/AuthContext";
import AdminTable from "../components/AdminTable";
import axios from "axios";
import { useNavigate } from "react-router-dom";

export default function Admin() {
  const { user } = useContext(AuthContext);
  const [users, setUsers] = useState([]);
  const [chats, setChats] = useState([]);
  const [query, setQuery] = useState("");
  const [activeTab, setActiveTab] = useState("users");
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();

  const loadData = useCallback(async () => {
    try {
      const [usersRes, chatsRes] = await Promise.all([
        getUsers(user.token),
        getChats(user.token),
      ]);
      setUsers(usersRes.data.users || []);
      setChats(chatsRes.data.chats || []);
    } catch (err) {
      console.error(err.response?.data || err.message);
    } finally {
      setLoading(false);
    }
  }, [user?.token]);

  const handleApprove = async (id) => {
    await approveUser(id, user.token);
    loadData();
  };

  const handleDeleteUser = async (id) => {
    if (window.confirm("Yakin ingin menghapus user ini?")) {
      await deleteUser(id, user.token);
      loadData();
    }
  };

  const handleDeleteChat = async (id) => {
    if (window.confirm("Yakin ingin menghapus chat ini?")) {
      await deleteChat(id, user.token);
      loadData();
    }
  };

  const toggleRole = async (id, newRole) => {
    if (window.confirm(`Yakin ingin mengubah role menjadi ${newRole}?`)) {
      await axios.put(
        `http://localhost:5000/api/admin/users/${id}/role`,
        { role: newRole },
        { headers: { Authorization: `Bearer ${user.token}` } }
      );
      loadData();
    }
  };

  const takeAction = async (id, action) => {
    const actionText = {
      warn: "memberi peringatan",
      mute: "mute selama 24 jam",
      ban: "ban permanen",
    };

    if (window.confirm(`Yakin ingin ${actionText[action]} user ini?`)) {
      await axios.post(
        `http://localhost:5000/api/reports/action`,
        { userId: id, action },
        { headers: { Authorization: `Bearer ${user.token}` } }
      );
      loadData();
    }
  };

useEffect(() => {
  if (user?.token) {
    loadData();
  }
}, [user?.token, loadData]);


  const filteredUsers = users.filter(
    (u) =>
      u.alias?.toLowerCase().includes(query.toLowerCase()) ||
      u.email?.toLowerCase().includes(query.toLowerCase())
  );

  const mutedUsers = users.filter(
    (u) => u.isMuted && new Date(u.muteUntil) > new Date()
  );
  const warnedUsers = users.filter((u) => u.warnCount > 0);
  const bannedUsers = users.filter((u) => u.isBanned);
  const pendingUsers = users.filter((u) => u.status === "pending");

  const getRemainingMute = (muteUntil) => {
    const diff = new Date(muteUntil) - new Date();
    if (diff <= 0) return "Expired";
    const hours = Math.floor(diff / (1000 * 60 * 60));
    const minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60));
    return `${hours}h ${minutes}m`;
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
          <p className="mt-4 text-gray-600">Loading dashboard...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <div className="bg-white shadow-sm border-b border-gray-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
          <h1 className="text-3xl font-bold text-gray-900">Admin Dashboard</h1>
          <p className="mt-1 text-sm text-gray-600">
            Kelola users, chat, dan laporan
          </p>
        </div>
      </div>

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
        {/* Stats Cards */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
          <div className="bg-white overflow-hidden shadow-sm rounded-lg border border-gray-200">
            <div className="p-5">
              <div className="flex items-center">
                <div className="flex-shrink-0">
                  <div className="w-8 h-8 bg-blue-500 rounded-lg flex items-center justify-center">
                    <span className="text-white text-sm"></span>
                  </div>
                </div>
                <div className="ml-5 w-0 flex-1">
                  <dl>
                    <dt className="text-sm font-medium text-gray-500 truncate">
                      Total Users
                    </dt>
                    <dd className="text-lg font-medium text-gray-900">
                      {users.length}
                    </dd>
                  </dl>
                </div>
              </div>
            </div>
          </div>

          <div className="bg-white overflow-hidden shadow-sm rounded-lg border border-gray-200">
            <div className="p-5">
              <div className="flex items-center">
                <div className="flex-shrink-0">
                  <div className="w-8 h-8 bg-yellow-500 rounded-lg flex items-center justify-center">
                    <span className="text-white text-sm"></span>
                  </div>
                </div>
                <div className="ml-5 w-0 flex-1">
                  <dl>
                    <dt className="text-sm font-medium text-gray-500 truncate">
                      Pending Approval
                    </dt>
                    <dd className="text-lg font-medium text-gray-900">
                      {pendingUsers.length}
                    </dd>
                  </dl>
                </div>
              </div>
            </div>
          </div>

          <div className="bg-white overflow-hidden shadow-sm rounded-lg border border-gray-200">
            <div className="p-5">
              <div className="flex items-center">
                <div className="flex-shrink-0">
                  <div className="w-8 h-8 bg-red-500 rounded-lg flex items-center justify-center">
                    <span className="text-white text-sm"></span>
                  </div>
                </div>
                <div className="ml-5 w-0 flex-1">
                  <dl>
                    <dt className="text-sm font-medium text-gray-500 truncate">
                      Warned Users
                    </dt>
                    <dd className="text-lg font-medium text-gray-900">
                      {warnedUsers.length}
                    </dd>
                  </dl>
                </div>
              </div>
            </div>
          </div>

          <div className="bg-white overflow-hidden shadow-sm rounded-lg border border-gray-200">
            <div className="p-5">
              <div className="flex items-center">
                <div className="flex-shrink-0">
                  <div className="w-8 h-8 bg-purple-500 rounded-lg flex items-center justify-center">
                    <span className="text-white text-sm"></span>
                  </div>
                </div>
                <div className="ml-5 w-0 flex-1">
                  <dl>
                    <dt className="text-sm font-medium text-gray-500 truncate">
                      Total Messages
                    </dt>
                    <dd className="text-lg font-medium text-gray-900">
                      {chats.length}
                    </dd>
                  </dl>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Quick Actions */}
        <div className="mb-8">
          <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
            <h3 className="text-lg font-medium text-gray-900 mb-4">
              Quick Actions
            </h3>
            <div className="flex flex-wrap gap-3">
              <button
                onClick={() => navigate("/admin-reports")}
                className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-orange-600 hover:bg-orange-700 transition-colors"
              >
                 Lihat Reports
              </button>
              <button
                onClick={() => navigate("/admin-online")}
                className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-green-600 hover:bg-green-700 transition-colors"
              >
                 Online Users
              </button>
              <button
                onClick={() => navigate("/admin-private")}
                className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-blue-600 hover:bg-blue-700 transition-colors"
              >
                 Private Chat
              </button>
            </div>
          </div>
        </div>

        {/* Tabs */}
        <div className="mb-6">
          <div className="border-b border-gray-200">
            <nav className="-mb-px flex space-x-8">
              <button
                onClick={() => setActiveTab("users")}
                className={`py-2 px-1 border-b-2 font-medium text-sm transition-colors ${
                  activeTab === "users"
                    ? "border-blue-500 text-blue-600"
                    : "border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300"
                }`}
              >
                 Users Management
              </button>
              <button
                onClick={() => setActiveTab("chats")}
                className={`py-2 px-1 border-b-2 font-medium text-sm transition-colors ${
                  activeTab === "chats"
                    ? "border-blue-500 text-blue-600"
                    : "border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300"
                }`}
              >
                 Chat Messages
              </button>
              <button
                onClick={() => setActiveTab("moderation")}
                className={`py-2 px-1 border-b-2 font-medium text-sm transition-colors ${
                  activeTab === "moderation"
                    ? "border-blue-500 text-blue-600"
                    : "border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300"
                }`}
              >
                 Moderation
              </button>
            </nav>
          </div>
        </div>

        {/* Tab Content */}
        {activeTab === "users" && (
          <div className="space-y-6">
            {/* Search */}
            <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
              <div className="flex items-center space-x-4">
                <div className="flex-1">
                  <div className="relative">
                    <input
                      type="text"
                      placeholder="Cari user berdasarkan nama atau email..."
                      value={query}
                      onChange={(e) => setQuery(e.target.value)}
                      className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none transition-all"
                    />
                    <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                      <svg
                        className="h-5 w-5 text-gray-400"
                        fill="none"
                        stroke="currentColor"
                        viewBox="0 0 24 24"
                      >
                        <path
                          strokeLinecap="round"
                          strokeLinejoin="round"
                          strokeWidth={2}
                          d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
                        />
                      </svg>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            {/* Users List */}
            <div className="space-y-4">
              {filteredUsers.length === 0 ? (
                <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-8 text-center">
                  <div className="text-4xl mb-4"></div>
                  <p className="text-gray-500">Tidak ada user yang ditemukan</p>
                </div>
              ) : (
                filteredUsers.map((u) => (
                  <div
                    key={u._id}
                    className="bg-white rounded-lg shadow-sm border border-gray-200 p-6 hover:shadow-md transition-shadow"
                  >
                    <div className="flex items-center justify-between">
                      <div className="flex items-center space-x-4">
                        <div className="w-12 h-12 bg-gray-300 rounded-full flex items-center justify-center">
                          <span className="text-lg font-medium text-gray-700">
                            {u.alias?.charAt(0)?.toUpperCase()}
                          </span>
                        </div>
                        <div>
                          <h3 className="text-lg font-medium text-gray-900">
                            {u.alias}
                          </h3>
                          <p className="text-sm text-gray-600">{u.email}</p>
                          <div className="flex items-center space-x-2 mt-1">
                            <span
                              className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${
                                u.status === "approved"
                                  ? "bg-green-100 text-green-800"
                                  : "bg-yellow-100 text-yellow-800"
                              }`}
                            >
                              {u.status}
                            </span>
                            <span
                              className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${
                                u.role === "admin"
                                  ? "bg-purple-100 text-purple-800"
                                  : "bg-blue-100 text-blue-800"
                              }`}
                            >
                              {u.role}
                            </span>
                            {u.warnCount > 0 && (
                              <span className="inline-flex px-2 py-1 text-xs font-semibold rounded-full bg-red-100 text-red-800">
                                 {u.warnCount} warning
                              </span>
                            )}
                            {u.isBanned && (
                              <span className="inline-flex px-2 py-1 text-xs font-semibold rounded-full bg-red-100 text-red-800">
                                 Banned
                              </span>
                            )}
                          </div>
                        </div>
                      </div>

                      <div className="flex items-center space-x-2">
                        {u.status === "pending" && (
                          <button
                            onClick={() => handleApprove(u._id)}
                            className="px-3 py-1 bg-green-600 hover:bg-green-700 text-white text-xs rounded-md transition-colors"
                          >
                             Approve
                          </button>
                        )}
                        <button
                          onClick={() =>
                            toggleRole(
                              u._id,
                              u.role === "admin" ? "user" : "admin"
                            )
                          }
                          className="px-3 py-1 bg-purple-600 hover:bg-purple-700 text-white text-xs rounded-md transition-colors"
                        >
                          {u.role === "admin"
                            ? " Make User"
                            : " Make Admin"}
                        </button>
                        <button
                          onClick={() => takeAction(u._id, "warn")}
                          className="px-3 py-1 bg-yellow-600 hover:bg-yellow-700 text-white text-xs rounded-md transition-colors"
                        >
                           Warn
                        </button>
                        <button
                          onClick={() => takeAction(u._id, "mute")}
                          className="px-3 py-1 bg-orange-600 hover:bg-orange-700 text-white text-xs rounded-md transition-colors"
                        >
                           Mute
                        </button>
                        <button
                          onClick={() => takeAction(u._id, "ban")}
                          className="px-3 py-1 bg-red-600 hover:bg-red-700 text-white text-xs rounded-md transition-colors"
                        >
                           Ban
                        </button>
                        <button
                          onClick={() => handleDeleteUser(u._id)}
                          className="px-3 py-1 bg-gray-600 hover:bg-gray-700 text-white text-xs rounded-md transition-colors"
                        >
                           Delete
                        </button>
                      </div>
                    </div>
                  </div>
                ))
              )}
            </div>
          </div>
        )}

        {activeTab === "chats" && (
          <div>
            <AdminTable data={chats} onDelete={handleDeleteChat} type="chat" />
          </div>
        )}

        {activeTab === "moderation" && (
          <div className="space-y-6">
            {/* Muted Users */}
            <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
              <h3 className="text-lg font-medium text-gray-900 mb-4 flex items-center">
                 Muted Users ({mutedUsers.length})
              </h3>
              {mutedUsers.length === 0 ? (
                <p className="text-gray-500 text-center py-4">
                  Tidak ada user yang dimute
                </p>
              ) : (
                <div className="space-y-3">
                  {mutedUsers.map((u) => (
                    <div
                      key={u._id}
                      className="flex items-center justify-between p-3 bg-orange-50 rounded-lg border border-orange-200"
                    >
                      <div className="flex items-center space-x-3">
                        <div className="w-8 h-8 bg-orange-300 rounded-full flex items-center justify-center">
                          <span className="text-sm font-medium text-orange-800">
                            {u.alias?.charAt(0)?.toUpperCase()}
                          </span>
                        </div>
                        <div>
                          <p className="font-medium text-gray-900">{u.alias}</p>
                          <p className="text-sm text-gray-600">
                            Sisa: {getRemainingMute(u.muteUntil)}
                          </p>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </div>

            {/* Warned Users */}
            <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
              <h3 className="text-lg font-medium text-gray-900 mb-4 flex items-center">
                 Warned Users ({warnedUsers.length})
              </h3>
              {warnedUsers.length === 0 ? (
                <p className="text-gray-500 text-center py-4">
                  Tidak ada user yang kena warning
                </p>
              ) : (
                <div className="space-y-3">
                  {warnedUsers.map((u) => (
                    <div
                      key={u._id}
                      className="flex items-center justify-between p-3 bg-yellow-50 rounded-lg border border-yellow-200"
                    >
                      <div className="flex items-center space-x-3">
                        <div className="w-8 h-8 bg-yellow-300 rounded-full flex items-center justify-center">
                          <span className="text-sm font-medium text-yellow-800">
                            {u.alias?.charAt(0)?.toUpperCase()}
                          </span>
                        </div>
                        <div>
                          <p className="font-medium text-gray-900">{u.alias}</p>
                          <p className="text-sm text-gray-600">
                            {u.warnCount} warning(s)
                          </p>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </div>

            {/* Banned Users */}
            <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
              <h3 className="text-lg font-medium text-gray-900 mb-4 flex items-center">
                 Banned Users ({bannedUsers.length})
              </h3>
              {bannedUsers.length === 0 ? (
                <p className="text-gray-500 text-center py-4">
                  Tidak ada user yang dibanned
                </p>
              ) : (
                <div className="space-y-3">
                  {bannedUsers.map((u) => (
                    <div
                      key={u._id}
                      className="flex items-center justify-between p-3 bg-red-50 rounded-lg border border-red-200"
                    >
                      <div className="flex items-center space-x-3">
                        <div className="w-8 h-8 bg-red-300 rounded-full flex items-center justify-center">
                          <span className="text-sm font-medium text-red-800">
                            {u.alias?.charAt(0)?.toUpperCase()}
                          </span>
                        </div>
                        <div>
                          <p className="font-medium text-gray-900">{u.alias}</p>
                          <p className="text-sm text-gray-600">{u.email}</p>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </div>
          </div>
        )}
      </div>
    </div>
  );
}
