import { useState, useEffect, useContext } from "react";
import { io } from "socket.io-client";
import { AuthContext } from "../context/AuthContext";
import axios from "axios";

const socket = io("http://localhost:5000");

export default function AdminOnline() {
  const { user } = useContext(AuthContext);
  const [onlineUsers, setOnlineUsers] = useState([]);
  const [allUsers, setAllUsers] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // Get all users data for additional info
    if (user?.token) {
      axios
        .get("http://localhost:5000/api/admin/users", {
          headers: { Authorization: `Bearer ${user.token}` },
        })
        .then((res) => {
          setAllUsers(res.data.users || []);
          setLoading(false);
        })
        .catch((err) => {
          console.error("Error fetching users:", err);
          setLoading(false);
        });
    }

    socket.on("onlineUsers", (users) => setOnlineUsers(users));

    return () => socket.off("onlineUsers");
  }, [user]);

  // Match online users with user data
  const onlineUsersData = onlineUsers.map((alias) => {
    const userData = allUsers.find((u) => u.alias === alias);
    return {
      alias,
      ...userData,
    };
  });

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-green-600 mx-auto"></div>
          <p className="mt-4 text-gray-600">Loading online users...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <div className="bg-white shadow-sm border-b border-gray-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-3xl font-bold text-gray-900">Online Users</h1>
              <p className="mt-1 text-sm text-gray-600">
                Monitor users yang sedang aktif di forum
              </p>
            </div>
            <div className="flex items-center space-x-2">
              <div className="w-3 h-3 bg-green-500 rounded-full animate-pulse"></div>
              <span className="text-lg font-semibold text-gray-900">
                {onlineUsers.length} Online
              </span>
            </div>
          </div>
        </div>
      </div>

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
        {/* Stats Card */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
          <div className="bg-white overflow-hidden shadow-sm rounded-lg border border-gray-200">
            <div className="p-5">
              <div className="flex items-center">
                <div className="flex-shrink-0">
                  <div className="w-8 h-8 bg-green-500 rounded-lg flex items-center justify-center">
                    <span className="text-white text-sm"></span>
                  </div>
                </div>
                <div className="ml-5 w-0 flex-1">
                  <dl>
                    <dt className="text-sm font-medium text-gray-500 truncate">
                      Users Online
                    </dt>
                    <dd className="text-lg font-medium text-gray-900">
                      {onlineUsers.length}
                    </dd>
                  </dl>
                </div>
              </div>
            </div>
          </div>

          <div className="bg-white overflow-hidden shadow-sm rounded-lg border border-gray-200">
            <div className="p-5">
              <div className="flex items-center">
                <div className="flex-shrink-0">
                  <div className="w-8 h-8 bg-blue-500 rounded-lg flex items-center justify-center">
                    <span className="text-white text-sm"></span>
                  </div>
                </div>
                <div className="ml-5 w-0 flex-1">
                  <dl>
                    <dt className="text-sm font-medium text-gray-500 truncate">
                      Total Users
                    </dt>
                    <dd className="text-lg font-medium text-gray-900">
                      {allUsers.length}
                    </dd>
                  </dl>
                </div>
              </div>
            </div>
          </div>

          <div className="bg-white overflow-hidden shadow-sm rounded-lg border border-gray-200">
            <div className="p-5">
              <div className="flex items-center">
                <div className="flex-shrink-0">
                  <div className="w-8 h-8 bg-purple-500 rounded-lg flex items-center justify-center">
                    <span className="text-white text-sm"></span>
                  </div>
                </div>
                <div className="ml-5 w-0 flex-1">
                  <dl>
                    <dt className="text-sm font-medium text-gray-500 truncate">
                      Online Rate
                    </dt>
                    <dd className="text-lg font-medium text-gray-900">
                      {allUsers.length > 0
                        ? Math.round(
                            (onlineUsers.length / allUsers.length) * 100
                          )
                        : 0}
                      %
                    </dd>
                  </dl>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Online Users List */}
        <div className="bg-white rounded-lg shadow-sm border border-gray-200">
          <div className="p-6 border-b border-gray-200">
            <h3 className="text-lg font-medium text-gray-900 flex items-center">
               Users Currently Online
              <span className="ml-2 px-2 py-1 bg-green-100 text-green-800 text-sm rounded-full">
                {onlineUsers.length}
              </span>
            </h3>
          </div>

          <div className="p-6">
            {onlineUsersData.length === 0 ? (
              <div className="text-center py-12">
                <div className="text-4xl mb-4"></div>
                <h3 className="text-lg font-medium text-gray-900 mb-2">
                  Tidak ada user online
                </h3>
                <p className="text-gray-500">
                  Semua user sedang offline saat ini
                </p>
              </div>
            ) : (
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                {onlineUsersData.map((u, index) => (
                  <div
                    key={index}
                    className="border border-gray-200 rounded-lg p-4 hover:shadow-md transition-shadow"
                  >
                    <div className="flex items-center space-x-3">
                      <div className="relative">
                        <div className="w-12 h-12 bg-gray-300 rounded-full flex items-center justify-center">
                          <span className="text-lg font-medium text-gray-700">
                            {u.alias?.charAt(0)?.toUpperCase()}
                          </span>
                        </div>
                        <div className="absolute -bottom-1 -right-1 w-4 h-4 bg-green-500 rounded-full border-2 border-white"></div>
                      </div>

                      <div className="flex-1 min-w-0">
                        <h4 className="text-sm font-medium text-gray-900 truncate">
                          {u.alias}
                        </h4>
                        <div className="flex items-center space-x-2 mt-1">
                          <span
                            className={`inline-flex px-2 py-0.5 text-xs font-semibold rounded-full ${
                              u.role === "admin"
                                ? "bg-purple-100 text-purple-700"
                                : "bg-blue-100 text-blue-700"
                            }`}
                          >
                            {u.role || "user"}
                          </span>

                          {u.warnCount > 0 && (
                            <span className="inline-flex px-2 py-0.5 text-xs font-semibold rounded-full bg-yellow-100 text-yellow-700">
                               {u.warnCount}
                            </span>
                          )}
                        </div>

                        {u.email && (
                          <p className="text-xs text-gray-500 mt-1 truncate">
                            {u.email}
                          </p>
                        )}
                      </div>
                    </div>

                    {/* Quick Actions for each user */}
                    {u._id && (
                      <div className="mt-3 pt-3 border-t border-gray-100">
                        <div className="flex space-x-1">
                          <button
                            onClick={() =>
                              window.open(
                                `/admin-private?user=${u._id}`,
                                "_blank"
                              )
                            }
                            className="flex-1 px-2 py-1 bg-blue-50 hover:bg-blue-100 text-blue-700 text-xs rounded transition-colors"
                          >
                             Chat
                          </button>
                          <button
                            onClick={() => {
                              // Quick action - could integrate with takeAction function
                              console.log("Quick action for user:", u._id);
                            }}
                            className="flex-1 px-2 py-1 bg-gray-50 hover:bg-gray-100 text-gray-700 text-xs rounded transition-colors"
                          >
                             Actions
                          </button>
                        </div>
                      </div>
                    )}
                  </div>
                ))}
              </div>
            )}
          </div>
        </div>

        {/* Auto Refresh Info */}
        <div className="mt-6 bg-blue-50 border border-blue-200 rounded-lg p-4">
          <div className="flex items-center">
            <div className="flex-shrink-0">
              <svg
                className="h-5 w-5 text-blue-400"
                viewBox="0 0 20 20"
                fill="currentColor"
              >
                <path
                  fillRule="evenodd"
                  d="M4 2a1 1 0 011 1v2.101a7.002 7.002 0 0111.601 2.566 1 1 0 11-1.885.666A5.002 5.002 0 005.999 7H9a1 1 0 010 2H4a1 1 0 01-1-1V3a1 1 0 011-1zm.008 9.057a1 1 0 011.276.61A5.002 5.002 0 0014.001 13H11a1 1 0 110-2h5a1 1 0 011 1v5a1 1 0 11-2 0v-2.101a7.002 7.002 0 01-11.601-2.566 1 1 0 01.61-1.276z"
                  clipRule="evenodd"
                />
              </svg>
            </div>
            <div className="ml-3">
              <p className="text-sm text-blue-800">
                Data online users diperbarui secara real-time melalui WebSocket
                connection.
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
