import { useState, useEffect, useContext, useRef } from "react";
import io from "socket.io-client";
import { AuthContext } from "../context/AuthContext";
import { getChats, sendChat } from "../services/chatService";
import ReportModal from "./ReportModal";

const socket = io("http://localhost:5000");

export default function ChatBox() {
  const { user } = useContext(AuthContext);
  const [message, setMessage] = useState("");
  const [messages, setMessages] = useState([]);
  const [showReport, setShowReport] = useState(false);
  const [reportTarget, setReportTarget] = useState(null);

  const messagesEndRef = useRef(null);

  // Ambil chat awal + listener socket
  useEffect(() => {
    if (user?.token) {
      getChats(user.token).then((res) => {
        if (Array.isArray(res.data.chats)) {
          setMessages(res.data.chats);
        } else {
          console.error(" chats bukan array:", res.data);
          setMessages([]);
        }
      });
    }

    socket.on("chatMessage", (msg) => {
      setMessages((prev) => [...prev, msg]);
    });

    return () => socket.off("chatMessage");
  }, [user]);

  // Kirim pesan
  const sendMessage = async () => {
    if (!message.trim()) return;
    try {
      const res = await sendChat({ message }, user.token);
      setMessages((prev) => [...prev, res.data]);
      socket.emit("chatMessage", res.data);
      setMessage("");
    } catch (err) {
      console.error("Error:", err);
    }
  };

  // Handle Enter key
  const handleKeyPress = (e) => {
    if (e.key === "Enter" && !e.shiftKey) {
      e.preventDefault();
      sendMessage();
    }
  };

  // Auto-scroll ke bawah tiap ada pesan baru
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  // Kondisi disable input
  const isMuted = user?.isMuted && new Date(user.muteUntil) > new Date();
  const isBanned = user?.isBanned;

  return (
    <div className="max-w-4xl mx-auto p-4">
      {/* Header */}
      <div className="mb-6">
        <h2 className="text-2xl font-bold text-gray-900 mb-2">Forum Chat</h2>
        <div className="h-1 w-20 bg-blue-600 rounded"></div>
      </div>

      {/* Warning banner */}
      {user?.warnCount > 0 && (
        <div className="bg-yellow-50 border-l-4 border-yellow-400 p-4 mb-6">
          <div className="flex">
            <div className="flex-shrink-0">
              <svg
                className="h-5 w-5 text-yellow-400"
                viewBox="0 0 20 20"
                fill="currentColor"
              >
                <path
                  fillRule="evenodd"
                  d="M8.257 3.099c.765-1.36 2.722-1.36 3.486 0l5.58 9.92c.75 1.334-.213 2.98-1.742 2.98H4.42c-1.53 0-2.493-1.646-1.743-2.98l5.58-9.92zM11 13a1 1 0 11-2 0 1 1 0 012 0zm-1-8a1 1 0 00-1 1v3a1 1 0 002 0V6a1 1 0 00-1-1z"
                  clipRule="evenodd"
                />
              </svg>
            </div>
            <div className="ml-3">
              <p className="text-sm text-yellow-700 font-medium">
                 Anda mendapat {user.warnCount} peringatan dari admin! Harap
                jaga ucapan Anda.
              </p>
            </div>
          </div>
        </div>
      )}

      {/* Mute/Ban status */}
      {isMuted && (
        <div className="bg-red-50 border border-red-200 rounded-md p-4 mb-6">
          <p className="text-red-800 text-sm">
             Anda tidak bisa chat sampai{" "}
            <span className="font-medium">
              {new Date(user.muteUntil).toLocaleString()}
            </span>
          </p>
        </div>
      )}

      {isBanned && (
        <div className="bg-red-50 border border-red-200 rounded-md p-4 mb-6">
          <p className="text-red-800 text-sm font-medium">
             Akun Anda sudah dibanned
          </p>
        </div>
      )}

      {/* Chat container */}
      <div className="bg-white rounded-lg shadow-sm border border-gray-200 overflow-hidden">
        {/* Messages area */}
        <div className="h-96 overflow-y-auto p-4 space-y-3 bg-gray-50">
          {messages.length === 0 ? (
            <div className="text-center text-gray-500 mt-20">
              <div className="text-4xl mb-2"></div>
              <p>Belum ada pesan. Mulai percakapan!</p>
            </div>
          ) : (
            messages.map((m, i) => {
              const isMe = m.alias === user.alias;
              const time = new Date(m.createdAt).toLocaleTimeString([], {
                hour: "2-digit",
                minute: "2-digit",
              });

              return (
                <div
                  key={i}
                  className={`flex ${isMe ? "justify-end" : "justify-start"}`}
                >
                  <div
                    className={`max-w-xs lg:max-w-md ${isMe ? "order-1" : ""}`}
                  >
                    {/* Avatar & Name */}
                    <div
                      className={`flex items-center mb-1 ${
                        isMe ? "justify-end" : ""
                      }`}
                    >
                      <div
                        className={`w-6 h-6 rounded-full flex items-center justify-center text-xs font-medium text-white ${
                          isMe ? "bg-blue-500 ml-2" : "bg-gray-500 mr-2"
                        }`}
                      >
                        {m.alias?.charAt(0)?.toUpperCase()}
                      </div>
                      <span className="text-xs text-gray-600 font-medium">
                        {m.alias}
                      </span>
                    </div>

                    {/* Message bubble */}
                    <div
                      className={`rounded-lg px-4 py-2 ${
                        isMe
                          ? "bg-blue-600 text-white"
                          : "bg-white text-gray-900 border border-gray-200"
                      }`}
                    >
                      <p className="text-sm leading-relaxed">{m.message}</p>

                      {/* Time & Report */}
                      <div
                        className={`flex items-center justify-between mt-2 ${
                          isMe ? "text-blue-100" : "text-gray-500"
                        }`}
                      >
                        <span className="text-xs">{time}</span>

                        {/* Report button - hanya untuk pesan orang lain */}
                        {!isMe && (
                          <button
                            onClick={() => {
                              setReportTarget({
                                messageId: m._id,
                                reportedId:
                                  m.from?._id || m.from || m.userId || m.sender,
                              });
                              setShowReport(true);
                            }}
                            className="text-xs text-red-500 hover:text-red-700 transition-colors"
                          >
                             Report
                          </button>
                        )}
                      </div>
                    </div>
                  </div>
                </div>
              );
            })
          )}
          <div ref={messagesEndRef} />
        </div>

        {/* Input area */}
        <div className="border-t border-gray-200 p-4 bg-white">
          <div className="flex space-x-3">
            <div className="flex-1">
              <input
                type="text"
                value={message}
                onChange={(e) => setMessage(e.target.value)}
                onKeyPress={handleKeyPress}
                placeholder={
                  user?.warnCount > 0
                    ? " Anda dalam pengawasan admin!"
                    : "Ketik pesan Anda..."
                }
                className={`w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none transition-all ${
                  user?.warnCount > 0
                    ? "border-yellow-300 bg-yellow-50"
                    : "border-gray-300"
                } ${
                  isMuted || isBanned
                    ? "bg-gray-100 cursor-not-allowed"
                    : "bg-white"
                }`}
                disabled={isMuted || isBanned}
              />
            </div>
            <button
              onClick={sendMessage}
              disabled={isMuted || isBanned || !message.trim()}
              className={`px-6 py-2 rounded-lg font-medium transition-all ${
                isMuted || isBanned || !message.trim()
                  ? "bg-gray-300 text-gray-500 cursor-not-allowed"
                  : "bg-blue-600 hover:bg-blue-700 text-white shadow-sm hover:shadow-md"
              }`}
            >
              Kirim
            </button>
          </div>
        </div>
      </div>

      {/* Modal Report */}
      {reportTarget && (
        <ReportModal
          show={showReport}
          onClose={() => setShowReport(false)}
          messageId={reportTarget.messageId}
          reportedId={reportTarget.reportedId}
          token={user.token}
        />
      )}
    </div>
  );
}
