import { useState, useContext, useEffect, useRef } from "react";
import { AuthContext } from "../context/AuthContext";
import axios from "axios";
import { io } from "socket.io-client";

const socket = io("http://localhost:5000");

export default function ChatAdmin() {
  const { user } = useContext(AuthContext);
  const [message, setMessage] = useState("");
  const [messages, setMessages] = useState([]);
  const [typingUser, setTypingUser] = useState(null);
  const messagesEndRef = useRef(null);
  let typingTimeout = null;

  useEffect(() => {
    if (!user?.id) return;
    socket.emit("joinRoom", user.id);

    axios
      .get(`http://localhost:5000/api/private/${user.id}`, {
        headers: { Authorization: `Bearer ${user.token}` },
      })
      .then((res) => setMessages(res.data));

    socket.on("privateMessage", (msg) => setMessages((prev) => [...prev, msg]));
    socket.on("typing", (alias) => setTypingUser(alias));
    socket.on("stopTyping", () => setTypingUser(null));
    socket.on("messageRead", (msgId) =>
      setMessages((prev) =>
        prev.map((m) => (m._id === msgId ? { ...m, status: "read" } : m))
      )
    );

    return () => socket.removeAllListeners();
  }, [user]);

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  const sendMessage = async () => {
    if (!message.trim()) return;
    await axios.post(
      "http://localhost:5000/api/private/send",
      { message },
      { headers: { Authorization: `Bearer ${user.token}` } }
    );
    setMessage("");
  };

  const handleTyping = (e) => {
    setMessage(e.target.value);
    socket.emit("typing", user.id, user.alias);
    clearTimeout(typingTimeout);
    typingTimeout = setTimeout(() => socket.emit("stopTyping", user.id), 2000);
  };

  const handleKeyPress = (e) => {
    if (e.key === "Enter" && !e.shiftKey) {
      e.preventDefault();
      sendMessage();
    }
  };

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <div className="bg-white shadow-sm border-b border-gray-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-3xl font-bold text-gray-900">
                Chat dengan Admin
              </h1>
              <p className="mt-1 text-sm text-gray-600">
                Hubungi admin untuk bantuan atau pertanyaan
              </p>
            </div>
            <div className="flex items-center space-x-2">
              <div className="w-3 h-3 bg-green-500 rounded-full animate-pulse"></div>
              <span className="text-sm text-gray-600">Admin Online</span>
            </div>
          </div>
        </div>
      </div>

      {/* Chat Container */}
      <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
        <div className="bg-white rounded-lg shadow-sm border border-gray-200 overflow-hidden">
          {/* Messages Area */}
          <div className="h-96 overflow-y-auto p-4 space-y-4 bg-gray-50">
            {messages.length === 0 ? (
              <div className="text-center text-gray-500 mt-20">
                <div className="text-4xl mb-4"></div>
                <h3 className="text-lg font-medium mb-2">
                  Belum ada percakapan
                </h3>
                <p className="text-sm">
                  Mulai chat dengan admin untuk mendapatkan bantuan
                </p>
              </div>
            ) : (
              messages.map((m) => {
                const isMe = m.from.role !== "admin";
                const time = new Date(m.createdAt).toLocaleTimeString([], {
                  hour: "2-digit",
                  minute: "2-digit",
                });

                return (
                  <div
                    key={m._id}
                    className={`flex ${isMe ? "justify-end" : "justify-start"}`}
                  >
                    <div
                      className={`max-w-xs lg:max-w-md ${
                        isMe ? "order-1" : ""
                      }`}
                    >
                      {/* Avatar & Name */}
                      <div
                        className={`flex items-center mb-1 ${
                          isMe ? "justify-end" : ""
                        }`}
                      >
                        <div
                          className={`w-8 h-8 rounded-full flex items-center justify-center text-sm font-medium text-white ${
                            isMe ? "bg-blue-500 ml-2" : "bg-green-500 mr-2"
                          }`}
                        >
                          {isMe ? "" : ""}
                        </div>
                        <span className="text-xs text-gray-600 font-medium">
                          {isMe ? "Anda" : m.from.alias}
                        </span>
                        {!isMe && (
                          <span className="ml-2 px-2 py-0.5 bg-green-100 text-green-700 text-xs rounded-full">
                            Admin
                          </span>
                        )}
                      </div>

                      {/* Message bubble */}
                      <div
                        className={`rounded-lg px-4 py-3 ${
                          isMe
                            ? "bg-blue-600 text-white"
                            : "bg-white text-gray-900 border border-gray-200"
                        }`}
                      >
                        <p className="text-sm leading-relaxed">{m.message}</p>

                        {/* Time & Status */}
                        <div
                          className={`flex items-center justify-between mt-2 ${
                            isMe ? "text-blue-100" : "text-gray-500"
                          }`}
                        >
                          <span className="text-xs">{time}</span>
                          <span className="text-xs">
                            {m.status === "read" ? "" : ""}
                          </span>
                        </div>
                      </div>
                    </div>
                  </div>
                );
              })
            )}
            <div ref={messagesEndRef} />
          </div>

          {/* Typing Indicator */}
          {typingUser && (
            <div className="px-4 py-2 bg-white border-t border-gray-100">
              <div className="flex items-center text-sm text-gray-500">
                <div className="flex space-x-1 mr-2">
                  <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce"></div>
                  <div
                    className="w-2 h-2 bg-gray-400 rounded-full animate-bounce"
                    style={{ animationDelay: "0.1s" }}
                  ></div>
                  <div
                    className="w-2 h-2 bg-gray-400 rounded-full animate-bounce"
                    style={{ animationDelay: "0.2s" }}
                  ></div>
                </div>
                <span className="italic">{typingUser} sedang mengetik...</span>
              </div>
            </div>
          )}

          {/* Input Area */}
          <div className="border-t border-gray-200 p-4 bg-white">
            <div className="flex space-x-3">
              <div className="flex-1">
                <input
                  type="text"
                  value={message}
                  onChange={handleTyping}
                  onKeyPress={handleKeyPress}
                  placeholder="Tulis pesan untuk admin..."
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none transition-all"
                />
              </div>
              <button
                onClick={sendMessage}
                disabled={!message.trim()}
                className={`px-6 py-2 rounded-lg font-medium transition-all ${
                  !message.trim()
                    ? "bg-gray-300 text-gray-500 cursor-not-allowed"
                    : "bg-blue-600 hover:bg-blue-700 text-white shadow-sm hover:shadow-md"
                }`}
              >
                <svg
                  className="w-4 h-4"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M12 19l9 2-9-18-9 18 9-2zm0 0v-8"
                  />
                </svg>
              </button>
            </div>
          </div>
        </div>

        {/* Help Section */}
        <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 pb-6">
          <div className="bg-amber-50 border border-amber-200 rounded-lg p-4">
            <div className="flex">
              <div className="flex-shrink-0">
                <svg
                  className="h-5 w-5 text-amber-400"
                  viewBox="0 0 20 20"
                  fill="currentColor"
                >
                  <path
                    fillRule="evenodd"
                    d="M8.257 3.099c.765-1.36 2.722-1.36 3.486 0l5.58 9.92c.75 1.334-.213 2.98-1.742 2.98H4.42c-1.53 0-2.493-1.646-1.743-2.98l5.58-9.92zM11 13a1 1 0 11-2 0 1 1 0 012 0zm-1-8a1 1 0 00-1 1v3a1 1 0 002 0V6a1 1 0 00-1-1z"
                    clipRule="evenodd"
                  />
                </svg>
              </div>
              <div className="ml-3">
                <h3 className="text-sm font-medium text-amber-800">Bantuan</h3>
                <div className="mt-2 text-sm text-amber-700">
                  <p>
                    Admin akan merespons pesan Anda secepatnya. Untuk bantuan
                    cepat, jelaskan masalah Anda dengan detail.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
