import { useState, useEffect, useContext } from "react";
import { AuthContext } from "../context/AuthContext";
import axios from "axios";

export default function AdminReports() {
  const { user } = useContext(AuthContext);
  const [reports, setReports] = useState([]);
  const [stats, setStats] = useState(null);
  const [loading, setLoading] = useState(true);
  const [activeTab, setActiveTab] = useState("pending");

  const getMuteTimeLeft = (muteUntil) => {
    if (!muteUntil) return null;
    const now = new Date();
    const end = new Date(muteUntil);
    const diff = end - now;
    if (diff <= 0) return null;
    const hours = Math.floor(diff / (1000 * 60 * 60));
    const mins = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60));
    return `${hours}h ${mins}m`;
  };

  useEffect(() => {
    if (!user?.token) return;

    const fetchData = async () => {
      try {
        const [reportsRes, statsRes] = await Promise.all([
          axios.get("http://localhost:5000/api/reports", {
            headers: { Authorization: `Bearer ${user.token}` },
          }),
          axios.get("http://localhost:5000/api/reports/stats", {
            headers: { Authorization: `Bearer ${user.token}` },
          }),
        ]);

        setReports(reportsRes.data);
        setStats(statsRes.data);
      } catch (err) {
        console.error("Error fetching reports:", err.response?.data || err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, [user]);

  const updateStatus = async (id, status) => {
    try {
      await axios.put(
        `http://localhost:5000/api/reports/${id}`,
        { status },
        { headers: { Authorization: `Bearer ${user.token}` } }
      );

      const reportsRes = await axios.get("http://localhost:5000/api/reports", {
        headers: { Authorization: `Bearer ${user.token}` },
      });
      setReports(reportsRes.data);
    } catch (err) {
      alert("Gagal update status: " + (err.response?.data?.msg || err.message));
    }
  };

  const takeAction = async (userId, action) => {
    const actionText = {
      warn: "memberi peringatan",
      mute: "mute selama 24 jam", 
      ban: "ban permanen"
    };

    if (window.confirm(`Yakin ingin ${actionText[action]} user ini?`)) {
      try {
        const res = await axios.post(
          "http://localhost:5000/api/reports/action",
          { userId, action },
          { headers: { Authorization: `Bearer ${user.token}` } }
        );
        alert(res.data.msg);

        const reportsRes = await axios.get("http://localhost:5000/api/reports", {
          headers: { Authorization: `Bearer ${user.token}` },
        });
        setReports(reportsRes.data);
      } catch (err) {
        alert("Gagal ambil tindakan: " + (err.response?.data?.msg || err.message));
      }
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
          <p className="mt-4 text-gray-600">Loading reports...</p>
        </div>
      </div>
    );
  }

  let filteredReports = reports.filter((r) => r.status === activeTab);
  filteredReports = filteredReports.filter((r) => !r.reportedId?.isBanned);

  const categoryIcons = {
    toxic: "",
    sara: "",
    pornografi: "",
    spam: "",
    scam: "",
    lainnya: ""
  };

  const statusColors = {
    pending: "bg-yellow-100 text-yellow-800 border-yellow-200",
    reviewed: "bg-blue-100 text-blue-800 border-blue-200",
    actionTaken: "bg-green-100 text-green-800 border-green-200"
  };

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <div className="bg-white shadow-sm border-b border-gray-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
          <h1 className="text-3xl font-bold text-gray-900">Reports Management</h1>
          <p className="mt-1 text-sm text-gray-600">
            Kelola laporan dari users dan ambil tindakan yang diperlukan
          </p>
        </div>
      </div>

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
        {/* Statistics Cards */}
        {stats && (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
            <div className="bg-white overflow-hidden shadow-sm rounded-lg border border-gray-200">
              <div className="p-5">
                <div className="flex items-center">
                  <div className="flex-shrink-0">
                    <div className="w-8 h-8 bg-gray-500 rounded-lg flex items-center justify-center">
                      <span className="text-white text-sm"></span>
                    </div>
                  </div>
                  <div className="ml-5 w-0 flex-1">
                    <dl>
                      <dt className="text-sm font-medium text-gray-500 truncate">Total Reports</dt>
                      <dd className="text-lg font-medium text-gray-900">{stats.total}</dd>
                    </dl>
                  </div>
                </div>
              </div>
            </div>

            <div className="bg-white overflow-hidden shadow-sm rounded-lg border border-gray-200">
              <div className="p-5">
                <div className="flex items-center">
                  <div className="flex-shrink-0">
                    <div className="w-8 h-8 bg-yellow-500 rounded-lg flex items-center justify-center">
                      <span className="text-white text-sm"></span>
                    </div>
                  </div>
                  <div className="ml-5 w-0 flex-1">
                    <dl>
                      <dt className="text-sm font-medium text-gray-500 truncate">Pending</dt>
                      <dd className="text-lg font-medium text-gray-900">{stats.pending}</dd>
                    </dl>
                  </div>
                </div>
              </div>
            </div>

            <div className="bg-white overflow-hidden shadow-sm rounded-lg border border-gray-200">
              <div className="p-5">
                <div className="flex items-center">
                  <div className="flex-shrink-0">
                    <div className="w-8 h-8 bg-blue-500 rounded-lg flex items-center justify-center">
                      <span className="text-white text-sm"></span>
                    </div>
                  </div>
                  <div className="ml-5 w-0 flex-1">
                    <dl>
                      <dt className="text-sm font-medium text-gray-500 truncate">Reviewed</dt>
                      <dd className="text-lg font-medium text-gray-900">{stats.reviewed}</dd>
                    </dl>
                  </div>
                </div>
              </div>
            </div>

            <div className="bg-white overflow-hidden shadow-sm rounded-lg border border-gray-200">
              <div className="p-5">
                <div className="flex items-center">
                  <div className="flex-shrink-0">
                    <div className="w-8 h-8 bg-green-500 rounded-lg flex items-center justify-center">
                      <span className="text-white text-sm"></span>
                    </div>
                  </div>
                  <div className="ml-5 w-0 flex-1">
                    <dl>
                      <dt className="text-sm font-medium text-gray-500 truncate">Action Taken</dt>
                      <dd className="text-lg font-medium text-gray-900">{stats.actionTaken}</dd>
                    </dl>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}

        {/* Tabs */}
        <div className="mb-6">
          <div className="border-b border-gray-200">
            <nav className="-mb-px flex space-x-8">
              <button
                onClick={() => setActiveTab("pending")}
                className={`py-2 px-1 border-b-2 font-medium text-sm transition-colors ${
                  activeTab === "pending"
                    ? "border-yellow-500 text-yellow-600"
                    : "border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300"
                }`}
              >
                 Pending ({stats?.pending || 0})
              </button>
              <button
                onClick={() => setActiveTab("reviewed")}
                className={`py-2 px-1 border-b-2 font-medium text-sm transition-colors ${
                  activeTab === "reviewed"
                    ? "border-blue-500 text-blue-600"
                    : "border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300"
                }`}
              >
                 Reviewed ({stats?.reviewed || 0})
              </button>
              <button
                onClick={() => setActiveTab("actionTaken")}
                className={`py-2 px-1 border-b-2 font-medium text-sm transition-colors ${
                  activeTab === "actionTaken"
                    ? "border-green-500 text-green-600"
                    : "border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300"
                }`}
              >
                 Action Taken ({stats?.actionTaken || 0})
              </button>
            </nav>
          </div>
        </div>

        {/* Reports List */}
        <div className="space-y-4">
          {filteredReports.length === 0 ? (
            <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-8 text-center">
              <div className="text-4xl mb-4"></div>
              <h3 className="text-lg font-medium text-gray-900 mb-2">
                Tidak ada laporan {activeTab}
              </h3>
              <p className="text-gray-500">Semua laporan sudah ditangani dengan baik!</p>
            </div>
          ) : (
            filteredReports.map((r) => (
              <div
                key={r._id}
                className="bg-white rounded-lg shadow-sm border border-gray-200 p-6 hover:shadow-md transition-shadow"
              >
                <div className="flex items-start justify-between">
                  <div className="flex-1">
                    {/* Header Info */}
                    <div className="flex items-center space-x-3 mb-4">
                      <span className="text-2xl">
                        {categoryIcons[r.reasonCategory] || ""}
                      </span>
                      <div>
                        <h3 className="text-lg font-medium text-gray-900 capitalize">
                          {r.reasonCategory}
                        </h3>
                        <div className="flex items-center space-x-4 text-sm text-gray-600">
                          <span>Reporter: <span className="font-medium">{r.reporterId}</span></span>
                          <span></span>
                          <span>Reported: <span className="font-medium">{r.reportedId?.alias || r.reportedId}</span></span>
                        </div>
                      </div>
                    </div>

                    {/* Message Content */}
                    <div className="bg-gray-50 rounded-lg p-4 mb-4">
                      <p className="text-sm font-medium text-gray-700 mb-2">Pesan yang dilaporkan:</p>
                      <p className="text-sm text-gray-900 italic">
                        "{r.messageText || "(pesan sudah dihapus)"}"
                      </p>
                    </div>

                    {/* Report Reason */}
                    {r.reason && (
                      <div className="mb-4">
                        <p className="text-sm font-medium text-gray-700 mb-1">Alasan laporan:</p>
                        <p className="text-sm text-gray-900">{r.reason}</p>
                      </div>
                    )}

                    {/* User Status Info */}
                    <div className="flex flex-wrap gap-2 mb-4">
                      <span className={`inline-flex px-3 py-1 text-xs font-semibold rounded-full border ${statusColors[r.status]}`}>
                        {r.status}
                      </span>
                      
                      {r.reportedId?.warnCount > 0 && (
                        <span className="inline-flex px-3 py-1 text-xs font-semibold rounded-full bg-orange-100 text-orange-800 border border-orange-200">
                           {r.reportedId.warnCount} warning(s)
                        </span>
                      )}
                      
                      {r.reportedId?.isMuted && r.reportedId?.muteUntil && (
                        <span className="inline-flex px-3 py-1 text-xs font-semibold rounded-full bg-red-100 text-red-800 border border-red-200">
                           Muted ({getMuteTimeLeft(r.reportedId.muteUntil)} left)
                        </span>
                      )}
                    </div>
                  </div>

                  {/* Actions */}
                  <div className="flex flex-col space-y-2 ml-6">
                    {activeTab === "pending" && (
                      <>
                        <button
                          onClick={() => updateStatus(r._id, "actionTaken")}
                          className="px-4 py-2 bg-green-600 hover:bg-green-700 text-white text-xs rounded-md transition-colors"
                        >
                           Action Taken
                        </button>
                      </>
                    )}

                    {activeTab === "reviewed" && (
                      <button
                        onClick={() => updateStatus(r._id, "actionTaken")}
                        className="px-4 py-2 bg-green-600 hover:bg-green-700 text-white text-xs rounded-md transition-colors"
                      >
                         Mark Action Taken
                      </button>
                    )}

                    {activeTab === "actionTaken" && (
                      <div className="space-y-2">
                        <button
                          onClick={() => takeAction(r.reportedId._id || r.reportedId, "warn")}
                          className="w-full px-4 py-2 bg-yellow-600 hover:bg-yellow-700 text-white text-xs rounded-md transition-colors"
                        >
                           Warn User
                        </button>
                        <button
                          onClick={() => takeAction(r.reportedId._id || r.reportedId, "mute")}
                          className="w-full px-4 py-2 bg-orange-600 hover:bg-orange-700 text-white text-xs rounded-md transition-colors"
                        >
                           Mute 24h
                        </button>
                        <button
                          onClick={() => takeAction(r.reportedId._id || r.reportedId, "ban")}
                          className="w-full px-4 py-2 bg-red-600 hover:bg-red-700 text-white text-xs rounded-md transition-colors"
                        >
                           Ban User
                        </button>
                      </div>
                    )}
                  </div>
                </div>

                {/* Report Date */}
                <div className="mt-4 pt-4 border-t border-gray-200">
                  <p className="text-xs text-gray-500">
                    Dilaporkan pada: {new Date(r.createdAt).toLocaleString()}
                  </p>
                </div>
              </div>
            ))
          )}
        </div>
      </div>
    </div>
  );
}